import { Box, Grid, GridItem, HStack, Heading, Image, Link, Text, VStack } from "@chakra-ui/react"

export const Home = () => {
    return (
        <>
            <Grid
                id="HomeGrid"
                h='900px'
                templateRows='repeat(2, 1fr)'
                templateColumns='repeat(10, 1fr)'
                gap={10}
                m='20px 20px 0 20px'
            >
                <GridItem colSpan={3} >
                    <img src="/image/home1.jpg" height='350px'></img>
                </GridItem>
                <GridItem colSpan={3} >
                    <img src="/image/home2.jpg"></img>
                </GridItem>
                <GridItem colSpan={4} rowSpan={2} textAlign='left' >
                    <Box mt='20'>
                        <VStack>
                            <Text fontSize='md'>  </Text>
                            <Heading size='3xl'>  </Heading>
                        </VStack>
                    </Box>
                    <Box mt='20'>
                        <Heading fontSize='xl'>(2023.04 ~ 2023.06) -  </Heading>
                        <Text fontSize='lg'>          .</Text>
                        <Text fontSize='lg'>   .</Text>
                    </Box>
                    <Box mt='8'>
                        <Heading fontSize='xl'>(2020.10 ~ 2023.03) -  </Heading>
                        <Text fontSize='lg'>MES , Smart Farm     .</Text>
                    </Box>
                    <HStack mt='8'>
                        <Link href="https://github.com/ssj5037" isExternal >
                            <Image src="https://github.githubassets.com/images/modules/site/icons/footer/github-mark.svg" />
                        </Link>
                    </HStack>
                </GridItem>

                <GridItem colSpan={3} >
                    <img src="/image/home3.jpg"></img>
                </GridItem>
                <GridItem colSpan={3} >
                    <img src="/image/home4.jpg"></img>
                </GridItem>
            </Grid>
        </>
    )
}